.slide__img-border {
  position: relative;
}

.slide__img-border:before, .slide__img-border:after {
  content: ' ';
  position: absolute;
  top: 0;
  left: 0;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: "GothamPro";
  font-style: normal;
  font-weight: 400;
  src: local("GothamPro"), local("GothamPro"), url("../fonts/GothamPro.woff2") format("woff2"), url("../fonts/GothamPro.woff") format("woff");
}

@font-face {
  font-family: "GothamPro";
  font-style: normal;
  font-weight: 500;
  src: local("GothamPro-Medium"), local("GothamPro-Medium"), url("../fonts/GothamPro-Medium.woff2") format("woff2"), url("../fonts/GothamPro-Medium.woff") format("woff");
}

.icon-close {
  width: 1em;
  height: 1em;
  fill: currentColor;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: currentColor;
}

.icon-mail {
  width: 1.36em;
  height: 1em;
  fill: currentColor;
}

.icon-map {
  width: 0.82em;
  height: 1em;
  fill: currentColor;
}

.icon-open {
  width: 1.77em;
  height: 1em;
  fill: currentColor;
}

.icon-phone {
  width: 1em;
  height: 1em;
  fill: currentColor;
}

.icon-search {
  width: 0.96em;
  height: 1em;
  fill: currentColor;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-size: 14px;
  font-family: "GothamPro", "Helvetica", sans-serif;
  font-weight: 400;
  color: #4e6e8a;
  background-color: #ebeef1;
  min-width: 320px;
}

a {
  text-decoration: none;
  color: #4e6e8a;
}

a:hover {
  text-decoration: none;
}

.out {
  overflow: hidden;
  position: relative;
  box-shadow: inset 0 150px 130px -50px #c6d0d9, inset 0 -150px 130px -50px #c6d0d9;
}

.container {
  max-width: 1350px;
  margin: 0 auto;
  padding: 0 30px;
}

.block {
  display: block;
}

.icon {
  font-size: 17px;
  color: #4e6e8a;
  transition: color 300ms;
}

.no-touch .icon:hover {
  color: #db812e;
}

.header {
  padding: 20px 0;
}

.header__container {
  display: flex;
}

.header__logo-img {
  width: 239px;
}

.header__menu {
  margin-left: auto;
}

.button {
  background-color: transparent;
  display: inline-block;
  color: #4e6e8a;
  font-family: "GothamPro", "Helvetica", sans-serif;
  cursor: pointer;
}

.menu {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.menu__top {
  display: flex;
  align-items: center;
  line-height: 1;
  margin-bottom: 6px;
}

.search {
  margin-right: 35px;
  position: relative;
}

.search__input {
  display: none;
  padding: 10px 15px;
  color: #4e6e8a;
  background-color: #d0dae3;
}

.search__input::placeholder {
  color: #4e6e8a;
}

.modal {
  margin-right: 35px;
}

.modal__btn {
  padding: 10px;
}

.no-touch .modal__btn:hover {
  color: #293a49;
}

.lang {
  position: relative;
}

.no-touch .lang__link:hover {
  color: #db812e;
}

.lang__current {
  border-bottom: 1px dotted #4e6e8a;
  position: relative;
  display: inline-block;
  width: 30px;
  margin-bottom: 5px;
}

.no-touch .lang__current:hover {
  color: #4e6e8a;
}

.lang__arrow {
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 5px 4px 0 4px;
  border-color: #4e6e8a transparent transparent transparent;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

.lang__list {
  display: none;
  position: absolute;
  z-index: 2;
  background-color: #ebeef1;
  width: 44px;
  height: 54px;
  left: -10px;
}

.lang__item {
  margin-top: 8px;
  margin-left: 10px;
}

.nav__list {
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 40px;
}

.nav__link {
  font-weight: 500;
  text-transform: uppercase;
  transition: color 200ms;
}

.no-touch .nav__link:hover {
  color: #db812e;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.main {
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.slider {
  max-width: 1100px;
  margin: 0 auto;
}

.slide {
  text-align: center;
  transition: border-color .3s, transform .3s;
}

.no-touch .slide:hover {
  transform: scale(1.1);
}

.no-touch .slide:hover .slide__img-border {
  border-color: transparent;
}

.no-touch .slide:hover .slide__img-border:before, .no-touch .slide:hover .slide__img-border:after {
  opacity: 1;
}

.no-touch .slide:hover .slide__title {
  color: #db812e;
}

.slide__link {
  display: block;
}

.slide__img-border {
  height: 390px;
  width: 390px;
  border-radius: 50%;
  border: 2px solid #4e6e8a;
  position: relative;
  margin: 0 auto;
  transition: border 300ms;
}

.slide__img-border:before {
  opacity: 0;
  transition: opacity 0.3s;
  background-image: url("../img/item-hover-sm.svg");
  background-size: 100%;
  background-repeat: no-repeat;
  width: 350px;
  height: 150px;
  top: 257px;
  transform: rotate(3deg);
  pointer-events: none;
}

.slide__img-border:after {
  opacity: 0;
  transition: opacity 0.3s;
  background-image: url("../img/item-hover-big.svg");
  background-size: contain;
  background-repeat: no-repeat;
  width: 350px;
  height: 450px;
  left: 60px;
  top: -27px;
  pointer-events: none;
}

.slide__img-wrap {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 364px;
  width: 364px;
  border-radius: 50%;
  overflow: hidden;
}

.slide__img {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 400px;
}

.slide__title {
  margin-top: 30px;
  transition: color 300ms;
  text-transform: uppercase;
}

.slide__img-1 {
  background-image: url("../img/car.png");
  background-size: 200%;
  background-position: 70% 60%;
}

.slide__img-2 {
  background-image: url("../img/html.png");
  background-position: 60% -10px;
  background-size: 140%;
}

.slide__img-3 {
  background-image: url("../img/lan.png");
  background-size: 150%;
  background-position: 34% -5%;
}

.slide__img-4 {
  background-image: url("../img/road.png");
  background-position: 60% -40px;
  background-size: 180%;
}

.slick-list {
  overflow: visible;
}

.title {
  font-size: 20px;
  font-weight: 500;
  color: #4e6e8a;
}

.footer {
  padding: 20px 0 30px;
}

.footer__container {
  display: flex;
  justify-content: space-between;
}

.contacts {
  display: flex;
}

.contacts__item:not(:last-child) {
  margin-right: 40px;
}

.no-touch .contacts__item:hover .icon-map, .no-touch .contacts__item:hover .icon-phone, .no-touch .contacts__item:hover .icon-mail {
  fill: #db812e;
}

.no-touch .contacts__item:hover .contacts__text {
  opacity: 0.7;
}

.contacts__link {
  padding: 5px 0;
}

.icon-map, .icon-phone, .icon-mail {
  position: relative;
  top: 2px;
  margin-right: 10px;
}

.popup {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(235, 238, 240, 0.9);
}

.popup__container {
  max-width: 1100px;
}

.slide_popup {
  transform: scale(1.2);
}

.no-touch .slide_popup:hover {
  transform: scale(1.2);
}

.slide_popup .slide__img-border {
  border: none;
}

.slide_popup .slide__img-border:before, .slide_popup .slide__img-border:after {
  opacity: 1;
}

.slide_popup .slide__title {
  color: #db812e;
}

.slide_popup__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(64, 91, 113, 0.7);
  color: #fff;
  font-size: 16px;
}

.slide_popup__description {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 300px;
}

[data-modal] {
  opacity: 0;
  visibility: hidden;
}

[data-modal] .is-open {
  visibility: visible;
  opacity: 1;
}
