
html {
	scroll-behavior: smooth; }
body {
	margin: 0;
	font-size: 14px;
	font-family: $gotham;
	font-weight: 400;
	color: $indigo;
	background-color: $bg;
	min-width: 320px; }

a {
	text-decoration: none;
	color: $indigo;
	&:hover {
		text-decoration: none; } }

