.out {
	overflow: hidden;
	position: relative;
	box-shadow: inset 0 150px 130px -50px #c6d0d9, inset 0 -150px 130px -50px #c6d0d9; }


.container {
	max-width: 1350px;
	margin: 0 auto;
	padding: 0 30px; }

.block {
	display: block; }

.icon {
	font-size: 17px;
	color: $indigo;
	@include tr(color 300ms);
	@include hover {
		color: $accent; } }

